@media (max-width: 756px) {
	.main {
	@include padding-margin(0 6rem, 6rem auto 3rem auto);
	}
}

@media (max-width: 636px) {
	.main {
		@include padding-margin(0 3rem, 3rem auto 3rem auto);
	}
	.h1--large {
		@include fontSize(45);
	}

}

 @media (max-width: 400px) {
	.h2--large {
		@include fontSize(21);
	}
}